<template>
  <div id="app">
    <mu-appbar class="header">
      <span class="header_logo"></span>
      <span class="header_lang" @click="changeLan" slot="right">{{english ? '' : 'EN'}}</span>
      <mu-menu slot="right">
        <mu-button flat>
          <img class="header_menu" src="./assets/menu.png" alt="">
        </mu-button>
        <mu-list slot="content">
          <mu-list-item link>
            <mu-list-item-content>
              <mu-list-item-title><a href="#home">{{english ? '' : 'Home'}}</a></mu-list-item-title>
            </mu-list-item-content>
          </mu-list-item>
          <mu-list-item link>
            <mu-list-item-content>
              <mu-list-item-title><a href="#obj">{{english ? '' : 'Rating Project'}}</a></mu-list-item-title>
            </mu-list-item-content>
          </mu-list-item>
          <mu-list-item link>
            <mu-list-item-content>
              <mu-list-item-title><a href="#contact">{{english ? '' : 'Contact'}}</a></mu-list-item-title>
            </mu-list-item-content>
          </mu-list-item>
        </mu-list>
      </mu-menu>
    </mu-appbar>
    <div id="home" class="home">
      <div class="home_box">
        <div class="home_box_name"></div>
        <div class="home_box_subname"></div>
        <div class="home_box_line"></div>
        <div class="home_box_slogan">
          ..
        </div>
      </div>
    </div>
    <div id="obj" class="obj">
      <div class="obj_box">
        <div class="obj_box_name"></div>
        <div class="obj_box_slogan">
          
        </div>
      </div>
    </div>
    <div id="contact" class="contact">
      <div class="contact_box">
        <div class="contact_box_name">
          
        </div>
        <div class="contact_icons_weixin">
          <div class="contact_icons_weixin_top">
            <div class="icon_wrap icon_wrap_weixin">
              <div class="icon_l icon_l_weixin"></div>
              <div class="icon_r"></div>
            </div>
          </div>
          <div class="contact_box_weixin"></div>
        </div>

        <div class="contact_icons_weibo">
          <div class="icon_wrap">
            <div class="icon_l icon_l_weibo"></div>
            <div class="icon_r"></div>
          </div>
          <a href="https://weibo.com" class="icon_link">https://weibo.com</a>
        </div>

        <div class="contact_icons_weibo">
          <div class="icon_wrap">
             <div class="icon_l icon_l_youxiang"></div>
              <div class="icon_r"></div>
          </div>
           <a href="https://zhenlian@163.com" class="icon_link">https://zhenlian@163.com</a>
        </div>
      </div>
    </div>
    <div class="footer">
      <div class="wrap">
        <div class="footer_box">2018 ZhenLian ICP030173 </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'app',
  data () {
    return {
      activeName: window.location.hash.substr(1,) || 'home',
      english: false,
    }
  },
  methods: {
    changeLan() {
      this.english = !this.english;
    }
  }
}
</script>

<style lang="scss">
@import "./reset.scss";

.header {
  background: transparent;
  position: relative;
  color: #fff;
  height: 3.2rem;
  line-height: 3.2rem;
  .mu-appbar-title {
    height: 3.2rem;
    line-height: 3.2rem;
  }
  &_logo {
    font-size: 1.5rem;
  }
  &_lang {
    font-size: 1rem;
    font-weight: 300;
  }
  &_menu {
    width: 1.5rem;
  }
}
.home {
  height: 42rem;
  margin-top: -3.2rem;
  width: 100%;
  padding: 0 1rem;
  background: url('./assets/bgtop.png') no-repeat center;
  background-size: cover;
  &_box {
    padding-top: 26.875rem;
    &_name {
      font-size: 1.6rem;
      color: #fff;
      font-weight: 500;
    }
    &_subname {
      font-size: 1rem;
      color: #fff;
      font-weight: 400;
      margin-top: 1rem;
    }
    &_line {
      width: 60px;
      height: 2px;
      background: #0694ea;
      margin: 2rem 0;
    }
    &_slogan {
      color: #fff;
      font-size: 22px;
      width: 240px;
      height: 42px;
      line-height: 42px;
      text-align: center;
      background: #2683ff;
    }
  }
}

.obj {
  height: 700px;
  width: 100%;
  background: url('./assets/bgbottom.png') no-repeat center;
  background-size: cover;
  &_box {
    padding-top: 260px;
    &_name {
      margin: 0 auto;
      width: 332px;
      height: 42px;
      background: url('./assets/bottom-name.png') no-repeat center;
      background-size: 100% 100%;
    }
    &_slogan {
      margin: 60px auto;
      color: #fff;
      font-size: 20px;
      text-align: center;
    }
  }
}

.contact {
  width: 100%;
  position: relative;
  &_box {
     padding-top: 5rem;
     &_name {
      background: url('./assets/line.png') no-repeat center;
      background-size: 100%;
      width: 200px;
      height: 42px;
      font-size: 1.6rem;
      line-height: 42px;
      text-align: center;
      margin: 0 auto;
     }

     &_weixin {
      width: 142px;
      height: 142px;
      background: #e2e3e5;
      margin-bottom: 10px;
     }
  }
}

.contact_icons_weixin {
  margin: 10px auto;
  width: 142px;
  &_top {
    margin-top: 50px;
    margin-bottom: 15px;
  }
}

.contact_icons_weibo {
  margin: 40px auto 0;
  width: 300px;
  text-align: center;
}

.icon_wrap {
  overflow: hidden;
  width: 120px;
  display: block;
  margin: 0 auto;
  .icon_l {
    float: left;
    width: 38px;
    height: 30px;
    margin: 0 auto;
    &_weixin {
      background: url('./assets/weixin.png') no-repeat center;
      background-size: cover;
      margin-left: 20px;
    }
    &_weibo {
      width: 38px;
      background: url('./assets/weibo.png') no-repeat center;
      background-size: cover;
      margin-left: 20px;
    }
    &_youxiang {
      width: 34px;
      height: 26px;
      background: url('./assets/youxiang.png') no-repeat center;
      background-size: 100%;
      margin-left: 20px;
    }
  }
  .icon_r {
    float: left;
    font-size: 20px;
    margin-left: 12px;
  }
}

.icon_link {
  display: block;
  margin-top: 10px;
  font-size: 20px;
  &:hover {
    color: #2683ff;
  }
}

.footer {
  height: 2.5rem;
  background: #626262;
  margin-top: 40px;
  &_box {
    line-height: 2.5rem;
    text-align: center;
    font-size: 0.875rem;
    font-weight: 400;
    color: #fff;
  }
}
</style>



